@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,500;0,600;1,500&display=swap);
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html { overflow-x: hidden !important; }

body {
  font-family: 'Space Grotesk', sans-serif;
  width: 100vw;
  background-color: #fff;
  overflow-x: hidden !important;
}

#monLogo { width: 40px;} /* LOGO RLPZ */

@-webkit-keyframes float {
  0% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
  50% {
    -webkit-transform: translatey(25px);
            transform: translatey(25px);
  }
  100% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
}

@keyframes float {
  0% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
  50% {
    -webkit-transform: translatey(25px);
            transform: translatey(25px);
  }
  100% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
}

/* COMP ANIM */
#line2 { animation-direction: reverse; }

#line2 span { margin: 0 50px 0; }

#line3 span { margin: 0 50px 0; }

#text .line:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

#line1 span:nth-child(odd), #line1 span:nth-child(even):hover, #line2 span:nth-child(odd), #line2 span:nth-child(even):hover, #line3 span:nth-child(odd), #line3 span:nth-child(even):hover {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

#line1 span:nth-child(odd):hover, #line2 span:nth-child(odd):hover, #line3 span:nth-child(odd):hover {
  color: black;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

@media (min-width: 768px) {
  #line2 span { margin: 0 90px 0; }
}

@-webkit-keyframes infiniteText {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes infiniteText {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}


/* FORM */
form {
  height: auto;
}
#name, #email {
  outline: none;
  width: 100%;
  padding-left: 20px;
  height: 3rem;
  border: 1px solid lightgray;
  background: transparent;
  margin: 0 auto 20px auto;
  box-shadow: 2px 2px 10px 2px #7a7a7a;
}

#name::-webkit-input-placeholder, #email::-webkit-input-placeholder, #message:-ms-input-placeholder {
  font-family: 'Space Grotesk', sans-serif;
}
#name:-ms-input-placeholder, #email:-ms-input-placeholder, #message:-ms-input-placeholder {
  font-family: 'Space Grotesk', sans-serif;
}
#name::-webkit-input-placeholder, #email::-webkit-input-placeholder, #message::-webkit-input-placeholder {
  font-family: 'Space Grotesk', sans-serif;
}
#name::placeholder, #email::placeholder, #message::placeholder {
  font-family: 'Space Grotesk', sans-serif;
}
.error::-webkit-input-placeholder {
  color: red;
  -webkit-transition: 200ms ease;
  transition: 200ms ease;
}
.error:-ms-input-placeholder {
  color: red;
  -ms-transition: 200ms ease;
  transition: 200ms ease;
}
.error::placeholder {
  color: red;
  transition: 200ms ease;
}

#errorEmail {
  color: red;
  font-size: 11px;
  display: none;
  transition: 1s ease-in-out;
  margin: -10px 0 20px 20px;
}

#message {
  width: 100%;
  height: 100px;
  resize: none;
  outline: none;
  padding: 5px 20px;
  border: 1px solid lightgray;
  background: transparent;
  margin: 0 auto 20px auto;
  box-shadow: 2px 2px 10px 2px #7a7a7a;
}

#formBtn {
  height: 2.5rem;
  width: 170px;
  font-family: 'Space Grotesk', sans-serif;
  text-transform: uppercase;
  background-color: black;
  color: white;
  border: transparent;
  box-shadow: 2px 2px 10px 2px #7a7a7a;
  cursor: pointer;
}

#formBtn:hover {
  background-color: red;
  transition: ease-in-out 0.3s;
  -webkit-transform: scaleZ(20%);
          transform: scaleZ(20%);
}

#formMessage {
  font-size: 12px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 10px;
  transition: 200ms ease;
  color: black;
  border-bottom: 1px dotted red;
  display: none;
}

/* IMG ANIM */
@-webkit-keyframes plane {
  0% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  50% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  100% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
}

@keyframes plane {
  0% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  50% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  100% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
}
